import React from 'react';
import TagCloud, { renderData } from 'd3-tagcloud-for-react';

class UDCloudTag extends React.Component {

  componentDidMount() {
    setInterval(() => {
      this.forceUpdate();
    }, 5000);
  }

  render() {
    return (
      <TagCloud
        className='tag-cloud'
        rotate={this.props.rotate}
        spiral={this.props.spiral}
        style={{
          fontFamily: this.props.fontFamily,
          fontSize: this.props.fontSize,
          padding: this.props.padding,
          width: '100%',
          height: this.props.height,
        }}
        data={this.props.data}
        colorarray={this.props.colorarray}
      >
        {renderData(this.props.data)}

      </TagCloud>
    )
  }
}

export default UDCloudTag
